}

.alarm_list ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 100px;
  gap: 16px;
}

.alarm_list li {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 268px;
