}

/* alarm step3 */

.list_btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 293px;
  height: 47px;
  border-radius: 16px;
  background: #cce0ff;
}

/* alarm list */

.alarm_list_wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.register_btn {
  display: flex;
