/* setting */
.setting_list_toggle a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 50px;
  padding: 14px 40px;
  background: #ecf7ff;
}

.setting_list_toggle h2 {
  font-size: 14px;
  font-weight: 300;
}

.setting_list_wrap {
  display: none;
  height: 240px;
  font-size: 14px;
}

.setting_list_item {
  display: flex;
  justify-content: space-between;
  padding: 20px 40px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.ck_slider {
  position: absolute;
  cursor: pointer;
  top: 1px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: #999;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.ck_slider:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 22px;
  left: -1px;
  bottom: -1px;
  background-color: #d9d9d9;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .ck_slider {
  background-color: #0047aa;
}

input:focus + .ck_slider {
  box-shadow: 0 0 1px #0047aa;
}

input:checked + .ck_slider:before {
  -webkit-transform: translateX(22px);
  -ms-transform: translateX(22px);
  transform: translateX(22px);
}

.ck_slider.round {
  border-radius: 10px;
}

.ck_slider.round:before {
  border-radius: 50%;
}

.setting_list_cont {
  font-size: 12px;
}

.open_btn {
  display: block;
  background: url(/public/static/images/arrow_down.png);
  width: 18px;
  height: 9px;
